import React from "react"
import { graphql, Link } from "gatsby"
import Layout from "../components/layout"
import TableOfContents from "../components/TableOfContents"
import * as styles from "./post.module.scss"
import kebabCase from "lodash/kebabCase"
import HtmlHead from "../components/head"

export default function Post({ data }) {
  const { title: siteTitle, author } = data.site.siteMetadata
  const { markdownRemark: post } = data
  let toc = ""
  if (post.tableOfContents) {
    toc = <TableOfContents items={post.tableOfContents.items} />
  }

  return (
    <Layout
      siteTitle={siteTitle}
      author={author}
      sidebar={toc}
      pageTitle={post.frontmatter.title}
      metaDescription={post.excerpt}
    >
      <div className={styles.postMain}>
        <h1 className={styles.postTitle}>{post.frontmatter.title}</h1>
        <ul className={styles.postMetas}>
          <li className="date">{post.frontmatter.date}</li>
          {post.frontmatter.update !== post.frontmatter.date && (
            <li className="update">Updated: {post.frontmatter.update}</li>
          )}
          <li className={styles.postMeta}>
            <Link
              to={`/category/${kebabCase(post.frontmatter.category)}`}
              className={styles.link}
            >
              <svg
                aria-hidden="true"
                focusable="false"
                data-prefix="fas"
                data-icon="tag"
                className="svg-inline--fa fa-tag fa-w-16"
                role="img"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 512 512"
                width="32"
                height="32"
              >
                <path
                  fill="currentColor"
                  d="M0 252.118V48C0 21.49 21.49 0 48 0h204.118a48 48 0 0 1 33.941 14.059l211.882 211.882c18.745 18.745 18.745 49.137 0 67.882L293.823 497.941c-18.745 18.745-49.137 18.745-67.882 0L14.059 286.059A48 48 0 0 1 0 252.118zM112 64c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48z"
                ></path>
              </svg>
              {post.frontmatter.category}
            </Link>
          </li>
          <li className={styles.postMeta}>
            {post.frontmatter.tags.map(tag => (
              <Link to={`/tag/${kebabCase(tag)}/`} className={styles.link}>
                <span>#{tag} </span>
              </Link>
            ))}
          </li>
        </ul>
        <div className={styles.markdown}>
          <div
            dangerouslySetInnerHTML={{ __html: post.html }}
            itemProp="articleBody"
          />
        </div>
      </div>
    </Layout>
  )
}

export const Head = ({ data }) => {
  return (< HtmlHead
    siteTitle={data.site.siteMetadata.title}
    pageTitle={data.markdownRemark.frontmatter.title}
    metaDescription={data.markdownRemark.frontmatter.excerpt}
  />)
}

export const pageQuery = graphql`
  query BlogPostByID($id: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      html
      tableOfContents(maxDepth: 2)
      excerpt
      frontmatter {
        title
        date(formatString: "YYYY.MM.DD")
        update(formatString: "YYYY.MM.DD")
        category
        tags
      }
      fields {
        slug
      }
    }
  }
`
